import React from 'react';
import { Link } from 'react-router-dom';

const Header = (props) => (
  <div>
    <header>Bitflyer(NPO)</header>
    <menu>
      <ul>
        <li><Link to="/">F#</Link></li>
        <li><Link to={"/transfer/" + props.data}></Link></li>
        <li><Link to={"/payment/" + props.data}></Link></li>
		{props.data}
      </ul>
    </menu>
  </div>
);

export default Header;


